body {
  margin: 0;
  background-color: #025669;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{
    font-size: 2.4rem;
    text-align: center;
    color: #fff;
}
.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-container-check {
    position: absolute;
    left: 12px;
  }
  .Icon-container-check--active {
    color: #4caf50;
  }
  
  .Icon-container-delete {
    position: absolute;
    right: 0;
  }
  .Icon-container-delete:hover {
    color: red;
  }
  
  .Icon-svg {
    width: 24px;
    height: 24px;
  }
  
  .Icon-container-check:hover .Icon-svg {
    fill: green;
  }
  .Icon-container-delete:hover .Icon-svg {
    fill: red;
  }
.TodoItem {
    width: 90%;
    height: auto;
    margin: 0% auto;
    background-color: #4db6b6;
    border-radius: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
.div--TodoSearch{
    display: grid;
    justify-content: center;
}
.div--TodoSearch input{
    width: 70vw;
    height: 3vh;
    background-color: #53ecec;
    border: solid #62dddd 2px;
    border-radius: 7px;
    padding: 5px 0px;
    font-size: 1.6rem;
    text-align: center;
    color: #fff;
}
.div--TodoSearch input:focus{

    outline-color:#53ecec;
}

.div--TodoSearch input::-webkit-input-placeholder{
    color: #fff;
}

.div--TodoSearch input::placeholder{
    color: #fff;
}
.TodoList{
    padding: 0px 5%;
}
.CreateTodoButton{
    height: 70px;
    width: 70px;
    z-index: 2;
    position: fixed;
    font-size: 50px;
    text-align: center;
    top: 90vh;
    left: 75vw;
    background-color:#62dddd;
    border: solid #53ecec 2px;
    border-radius: 100%;
    color: #fff;
}

.CreateTodoButton:hover{
    cursor: pointer;
}

 @media (min-width: 520px) {
    .CreateTodoButton{
        left: 87vw;
    }
 }
ul li {
    width: 90%;
    height: 80px;
    margin: 0% auto;
    background-color: rgb(101, 101, 101);
    border-radius: 10px;
    display: flex;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(2, 2, 2, 0.15);
  }
  
.EmpetyTodo--h1{
    width: 90vw;
    height: 50vh;
    color: #fff;
    display: grid;
    place-content: center;
    font-size: 5.8rem;
}
.ModalBackground {
  background-color: rgba(32,35,41,.8);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
form
{
    width:90%;
    max-width:300px;
    background-color:#fff;
    padding:33px 40px;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column
}

label{
    text-align:center;
    font-weight:700;
    font-size:20px;
    color:#1e1e1f;
    margin-bottom:26px
}

textarea{background-color:#f9fbfc;
    border:2px solid #202329;
    border-radius:2px;
    box-shadow:0 5px 50px rgba(32,35,41,.25);
    color:#1e1e1f;font-size:20px;
    text-align:center;padding:12px;
    height:96px;width:calc(100% - 25px)
}
textarea::-webkit-input-placeholder{
    color:#a5a5a5;
    font-family:montserrat;
    font-weight:400
}
textarea::placeholder{
    color:#a5a5a5;
    font-family:montserrat;
    font-weight:400
}

textarea:focus{
    outline-color:#61dafa
}

.TodoForm-buttonContainer{
    margin-top:14px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:100%
}

.TodoForm-button{
    cursor:pointer;
    display:inline-block;
    font-size:20px;
    color:#202329;
    font-weight:400;
    width:120px;
    height:48px;
    border-radius:2px;
    border:none;
    font-family:montserrat
}

.TodoForm-button--add{
    background:#61dafa;
    box-shadow:0 5px 25px rgba(97,218,250,.5)

}
.TodoForm-button--cancel{
    background:0 0
}
